<script>
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  
  export default {
    name: 'answer-career',
    data () {
      return {
        career: {},
        answer: '',

        loaded: false        
      }
    },
    components: {
      Loading
    },
    created () {
      api.get(`careers/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.career = res
        this.id = res.id
        this.answer = res.answer

        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'careers' })
      })
    },
    methods: {
      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = { answer: this.answer }

            api.put(`careers/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('career.updated'), type: 'success' }
              )
              this.$router.push({ name: 'careers'})
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

